.playlist-container {
  background-color: black;
  color: white;
  display: grid;
  grid-template-columns: 55rem 10rem;
  grid-gap: 1rem;

  .playlist-element {
    .glow {
      font-size: 50px;
      display: block;
      width: 90vw;
      margin: 20px;
      text-align: center;
      background-image: linear-gradient(to right,
          #51e2f5,
          #9df9ef,
          #edf756,
          #ffa8b6,
          #a28089);
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
      color: transparent;
    }

    .bar {
      padding: 5px;
      display: inline-flex;
      justify-items: center;
      align-items: center;
      backdrop-filter: blur(100px);
      border: 6px solid white;
      border-top-left-radius: 49px;
      border-bottom-left-radius: 49px;
      border-top-right-radius: 48px;
      border-bottom-right-radius: 48px;
      height: 77px;
      margin: 30px 40px;
      cursor: none;
      background-image: linear-gradient(to left,
          #51e2f5,
          #9df9ef,
          #edf756,
          #ffa8b6,
          #a28089);

      img {
        border-radius: 50%;
        border: 2px solid white;
        height: 70px;
        width: 70px;
        margin: 0 10px;
        cursor: none;
      }

      audio {
        position: relative;
        bottom: 22px;
      }
    }
  }
}

