.btn {
  padding: 20px;
  background-color: black;

  .btn-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .shuffle-arrow {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .arrows {
    width: 6vw;
    height: 12.5vh;
    padding: 10px;
    cursor: pointer;
    margin: 20px 50px;
    border: 4px solid white;
    border-radius: 50%;
    background-image: linear-gradient(to left,
        #51e2f5,
        #9df9ef,
        #edf756,
        #ffa8b6,
        #a28089);
    -moz-background-clip: content-box;
    background-clip: content-box;
    color: transparent;
  }
}